import React, { useState } from 'react';
import './App.css';
import dataFromJson from './mandf.json';



function App() {

  const [randomNames, setrandomNames] =useState([]);
  const male = dataFromJson[0].male.sort();
  const female = dataFromJson[0].female.sort();

  

  const randomFunctionMale = () => {
    var fullNameM = "";
    for (let index = 0; index <= Math.floor(Math.random() * 3); index++) {
      fullNameM = fullNameM + " "+ male[Math.floor(Math.random() * male.length)];
    }
    setrandomNames([...randomNames, { nameM: "Mg "+fullNameM }]);
  }

  const randomFunctionFemale = () => {
    var fullNameF = "";
    for (let index = 0; index <= Math.floor(Math.random() * 3); index++) {
      fullNameF = fullNameF + " "+female[Math.floor(Math.random() * female.length)];
    }
    setrandomNames([...randomNames, { nameM: 'Ma ' + fullNameF }]);
  }





  var count1=0;
  var count2=0;
  var count3=0;

  return (
    <div id="content"><div id="intro">Burmese Name Generator</div>
        <div id="malelist">
          <h4 className="listIM">Male Names</h4>
          <select>
            {
              
              male.map(gg => {
              count1++;
              return (<option key={count1} >{count1}.{gg}</option>)
              
              })
            }
          </select>
        </div>
        <div id="femalelist">
          <h3 className="listIF">Female Names</h3>
          <select>
            {
              female.map(gg => {
              count2++;
              return (<option key={count2}>{count2}.{gg}</option>)
              })
            }
          </select>
        </div>
       <div  id="mM">
        <p>Male</p>
        <button onClick={randomFunctionMale} >Generate</button>
       </div>
        <div id="mF">
        <p>Female</p>
        <button onClick={randomFunctionFemale} >Generate</button>
        </div>
       <div id="displaybox">
       <div id="alert" ><p>Your random names will be appeared in this drop-down list after clicking the buttons</p></div>
        <select id="dd-list">
            {
              randomNames.map(gg => {
                count3++;
              return (<option selected key={count3} >{count3}.{gg.nameM}</option>)
              })
            }
        </select>
       </div>
        <div className="email">Welcome Suggestions
        <br />
        <a id="em" href = "mailto: nyilynnhtwe@gmail.com">
        Email
        </a>
        <br />
        <div id= "reff"> <a href="https://nyilynnhwe.github.io/ref_for_rdnames.html">Reference</a></div>
        <div id="cright">Florject&copy;{new Date().getFullYear()}</div>

        </div>  
    </div>
  );
}

export default App;





